.playerI-section .card-border ,
.playerII-section .card-border {
  background-color: white;
  box-shadow: 0 0 10px black;
  border-radius: 10px;
  padding: 8px;
}

.playerI-section .card-background-image ,
.playerII-section .card-background-image  {
  font-family: 'Acme', sans-serif;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 10px;
}

.playerI-section .card-gradient ,
.playerII-section .card-gradient {
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: linear-gradient(
    45deg,
    rgb(25, 43, 104, 0.7),
    rgb(65, 199, 220, 0.7),
    rgb(25, 43, 104, 0.7)
  );
  height: 420px;
  width: 300px;
}

.playerI-section .trunfo-logo-border ,
.playerII-section .trunfo-logo-border {
  font-size: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(0, 0, 0);
  transform: rotate(-20deg);
  border: 4px solid white;
  border-radius: 50%;
  width: 150px;
  height: 150px;
}

@media screen and (max-width: 750px) {
  .player-card .card-gradient {
    height: 320px;
    width: 220px;
  }

  .player-card .trunfo-logo-border {
  font-size: 24px;
  width: 120px;
  height: 120px;
  }
}

@media screen and (max-width:550px) {
  .player-card .card-gradient{
    height: 250px;
    width: 160px;
  }

  .player-card .trunfo-logo-border {
    font-size: 20px;
    width: 100px;
    height: 100px;
    }
}